import React from 'react';
import Pokedex from './Pokedex';

const Pokegame = () => {
  const POKE_DATA = [
    { id: 4, name: 'Charmander', type: 'fire', base_experience: 62 },
    { id: 7, name: 'Squirtle', type: 'water', base_experience: 63 },
    { id: 11, name: 'Metapod', type: 'bug', base_experience: 72 },
    { id: 12, name: 'Butterfree', type: 'flying', base_experience: 178 },
    { id: 25, name: 'Pikachu', type: 'electric', base_experience: 112 },
    { id: 39, name: 'Jigglypuff', type: 'normal', base_experience: 95 },
    { id: 94, name: 'Gengar', type: 'poison', base_experience: 225 },
    { id: 133, name: 'Eevee', type: 'normal', base_experience: 65 }
  ];

  let hand_one = [];
  let hand_two = [...POKE_DATA];

  while (hand_one.length < hand_two.length) {
    const random_item = hand_two[Math.floor(Math.random() * hand_two.length)];
    const index = hand_two.indexOf(random_item);
    hand_two.splice(index, 1);
    hand_one = [...hand_one, random_item];
  }

  const sum = array =>
    array.reduce((exp, pokemon) => exp + pokemon.base_experience, 0);

  const hand_one_score = sum(hand_one);
  const hand_two_score = sum(hand_two);

  return (
    <div>
      <Pokedex
        pokemons={hand_one}
        total_exp={hand_one_score}
        isWinner={hand_one_score > hand_two_score}
      />
      <Pokedex
        pokemons={hand_two}
        total_exp={hand_two_score}
        isWinner={hand_two_score > hand_one_score}
      />
    </div>
  );
};

export default Pokegame;
